import HeaderSlideshow from "./SlideshowParts/HeaderSlideshow";
import PanelSlideshow from "./SlideshowParts/PanelSlideshow";
import styles from "./SlideshowPage.module.scss";
import { SlideshowType } from "./dataTypes";

import { useState } from "react";

const SlideshowPage = () => {
  const slideshowPlaceholder: SlideshowType = {
    slideshow: {
      workDayEnd: "23:30",
      workDayStart: "00:00",
      slides: [],
      companyLogoUrl: "Url placeholder",
      projectId: "0000000",
      projectName: "placeholder",
    },
    calendarEvents: [],
  };

  const slideshowSmartTvTest = {
    "slideshow": {
        "projectName": "Astoria",
        "slides": [
            {
                "type": "Image",
                "file": {
                    "name": "Områdeszoner.JPG",
                    "sequence": 0,
                    "url": "https://construction-test.myloc.se/mylocTEST/rs/construction/v1/files/1528445434074/download?sessionid=77%2B977%2B977%2B9Xe%2B%2FvX3vv719BGA377%2B9TS7vv73vv70%3D",
                    "createdTimestamp": 1528445434090,
                    "id": "1528445434074"
                },
                "sequence": 0,
                "displayTime": 5
            },
            {
                "type": "ExternalPage",
                "externalUrl": "https://myloc.se/sv/start/",
                "sequence": 1,
                "displayTime": 3
            },
            {
                "type": "calendar",
                "viewMode": "timeGridWeek",
                "startDate": "2022-07-13",
                "showWeekends": true,
                "calendarEvents": {
                    "count": 2,
                    "ids": [
                        "OTS¤337336¤10",
                        "OTS¤337334¤10"
                    ]
                },
                "sequence": 2,
                "displayTime": 15
            },
            {
                "type": "calendar",
                "viewMode": "timeGridWeek",
                "startDate": "2022-07-20",
                "showWeekends": true,
                "calendarEvents": {
                    "count": 0,
                    "ids": []
                },
                "sequence": 3,
                "displayTime": 15
            },
            {
                "type": "ExternalPage",
                "externalUrl": "https://myloc.se/sv/start/",
                "sequence": 4,
                "displayTime": 4
            },
        ],
        "workDayEnd": "23:30",
        "workDayStart": "00:00",
        "projectId": "2300200",
        "companyLogoUrl": "https://construction-test.myloc.se/mylocTEST/images/Logos/ARCONA.jpg"
    },
    "calendarEvents": [
        {
            "pattern": "blank",
            "backgroundColor": "#FFF",
            "end": "2022-07-13T22:00:00",
            "start": "2022-07-13T02:30:00",
            "icon": "redWarning",
            "title": "Midcraft",
            "bodyText": " - Test\n\nkalender.astoria@arcona.se",
            "headerColor": "#EFEFEF",
            "allDay": false,
            "textColor": "#000",
            "id": "OTS¤337336¤10"
        },
        {
            "pattern": "bubbles",
            "backgroundColor": "#FBE983",
            "end": "2022-07-13T13:30:00",
            "start": "2022-07-13T12:00:00",
            "icon": "redWarning",
            "title": "ATAB",
            "bodyText": "B1-M - Testar bildspel\n\narcona.myloc@olivetree.se",
            "headerColor": "#EFEFEF",
            "allDay": false,
            "textColor": "#000",
            "id": "OTS¤337334¤10"
        }
    ]
}

  const validateSlideshowData = (slideshowData: string): boolean => {
    const indicters = [
      "slideshow",
      "workDayEnd",
      "workDayStart",
      "companyLogoUrl",
      "projectId",
      "projectName",
      "slides",
      "calendarEvents",
    ];

    return indicters.every(key => slideshowData.includes(key));
  };

  const [slideshowInfo, setSlideshowInfo] = useState<SlideshowType>(() => {
    const getSlideshowData = window.localStorage.getItem("slides");

    if (getSlideshowData === null || !validateSlideshowData(getSlideshowData)) {
      return slideshowSmartTvTest;
    } else {
      return JSON.parse(getSlideshowData) as SlideshowType;
    }
  });

  

  return (
    <div className={styles.container}>
      {slideshowInfo !== slideshowPlaceholder ? (
        <>
          <HeaderSlideshow
            projectName={slideshowInfo.slideshow.projectName}
            projectID={slideshowInfo.slideshow.projectId}
            companyLogo={slideshowInfo.slideshow.companyLogoUrl}
          />
          <PanelSlideshow
            slides={slideshowInfo.slideshow.slides}
            tempWorkDayTime={[slideshowInfo.slideshow.workDayStart, slideshowInfo.slideshow.workDayEnd]}
            calendarEvents={slideshowInfo.calendarEvents}
          />
        </>
      ) : (
        <div
          style={{
            textAlign: "center",
            fontSize: "80px",
          }}
        >
          Server connection problems <br />
          Please waiting and refreshing
        </div>
      )}
    </div>
  );
};

export default SlideshowPage;
