import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import enLocale from "@fullcalendar/core/locales/en-gb";
/* import svLocale from "@fullcalendar/core/locales/sv"; */
/* import nbLocale from "@fullcalendar/core/locales/nb"; */

import SmallCalendarEvent from "./CalenderSlideshow/SmallCalendarEvent";
import NormalCalendarEvent from "./CalenderSlideshow/NormalCalendarEvent";
import { CalenderEventsCalendar, Slides } from "../../dataTypes";

import differenceInMinutes from "date-fns/differenceInMinutes";

const Calendar = ({
  workDayStart,
  workDayEnd,
  slide,
  calendarEvent,
}: {
  workDayStart: string;
  workDayEnd: string;
  slide: Slides;
  calendarEvent: CalenderEventsCalendar[];
}) => {
  function timeDuration(startDate: Date, endDate: Date) {
    return differenceInMinutes(endDate, startDate);
  }

  return (
    <div>
      <FullCalendar
        plugins={[timeGridPlugin, dayGridPlugin]}
        initialView={slide.viewMode}
        weekends={slide.showWeekends}
        initialDate={slide.startDate}
        locale={enLocale}
        headerToolbar={false}
        firstDay={1}
        slotMinTime={workDayStart}
        slotMaxTime={workDayEnd}
        height={window.innerHeight > 600 ? "calc(100vh - 80px)" : "100vh"}
        expandRows={true}
        nowIndicator={true} // temp
        slotDuration={
          timeDuration(new Date(`1995-12-17T${workDayStart}`), new Date(`1995-12-17T${workDayEnd}`)) > 9 * 60
            ? "02:00"
            : "00:30"
        }
        events={calendarEvent}
        eventContent={function (arg) {
          return (
            <>
              {timeDuration(arg.event.start!, arg.event.end!) > 90 ? (
                <NormalCalendarEvent arg={arg} />
              ) : (
                <SmallCalendarEvent arg={arg} />
              )}
            </>
          );
        }}
      />
    </div>
  );
};

export default Calendar;
