import styles from "./HeaderSlideshow.module.scss";

const HeaderSlideshow = ({
  projectName,
  projectID,
  companyLogo,
}: {
  projectName: string;
  projectID: string;
  companyLogo: string;
}) => {
  return (
    <div className={styles.header__slideshow}>
      <img
        className={styles.header__myloc}
        src="https://myloc.se/wp-content/uploads/2016/11/mylocconstransp..png"
        alt="MyLoc"
      />

      <div className={styles.header__kund}>
        <div className={styles.header__text}>
          {projectID} - {projectName}
        </div>

        <img className={styles.companyLogo} src={companyLogo} alt={projectName} />
      </div>
    </div>
  );
};

export default HeaderSlideshow;
